export default {
	data() {
		return {
			url:this.url1,
			txt:'',
			xunXiang:false,
			lbt : [
				
			],
			fenLeiActive:1,
			goShopping:null,
			shop:null,
			cnxh:[],
			showColor:[],
			useUser:this.user
		}
	},
	onLoad(options) {
		
		console.log(options)
		this.goShopping=JSON.parse(options.goShopping);
		this.goShopping.label=JSON.parse(this.goShopping.label);
		this.goShopping.shopType=JSON.parse(this.goShopping.shopType);
		this.shop=JSON.parse(options.shop);
		this.getshop();
	},
	methods: {
		
		gouWuChe:function(){
			if(this.user=='未登录'){
				uni.navigateTo({
					url:'../../login/index/index'
				})
			}else{
				if(this.useUser.uid!=this.shop.uid){
					if(!this.showXunXiang()){
						let that=this;
						let usurl=that.url1+"/dinghai/Shopping/insertAddress";
						uni.showLoading({
							title:'正在添加',mask: true
						})
						uni.request({
									url: usurl,
									header:{
										"Content-Type" : "application/x-www-form-urlencoded"
									},
									method:'POST',
									data: {
										commodityid:that.goShopping.id,
										commodityname: that.goShopping.name,
										shoppingnum:1,
										shoppingprice:that.goShopping.price,
										uid:that.useUser.uid,
										shopid:that.shop.id,
										shopimg:that.goShopping.img1[0],
										shopname:that.shop.name,
										shoptype:that.txt,
										freight:that.goShopping.freight,
										sid:that.goShopping.uid
									},
							    success: (res) => {
									console.log(res)
									if(res.statusCode==200){
										uni.showToast({
											title:"添加购物车成功"
										})
										uni.navigateBack({
											
										})
									}else{
										uni.showToast({
											title:"您的网络打了个盹",
											icon:"none"
										})
									}
							    },
								fail : function(err){
									console.log(err)
									uni.showToast({
										title:"您的网络打了个盹",
										icon:"none"
									})
								}
							});
					}
				}else{
					uni.showToast({
						title:'不可以把自己的商品添加购物车哦',
						icon:'none'
					})
				}
			}
		},
		liJiGouMai:function(){
			if(this.user=='未登录'){
				uni.navigateTo({
					url:'../../login/index/index'
				})
			}else{
				let that=this;
				if(this.useUser.uid!=this.shop.uid){
					if(!this.showXunXiang()){
						uni.redirectTo({
							url:'liJiGouMai/liJiGoMai?goShopping='+JSON.stringify(that.goShopping)+'&shop='+JSON.stringify(that.shop)+'&txt='+JSON.stringify(that.showColor)
						})
					}
				}else{
					uni.showToast({
						title:'不可以购买自己的商品哦',
						icon:'none'
					})
				}
			}
		},
		showXunXiang:function(){
			if(this.showColor.length==this.goShopping.shopType.length){
				for(let i=0;i<this.showColor.length;i++){
					if(!this.showColor[i]){
				this.xunXiang=true;
						return true
					}
				}
			}else{
				this.xunXiang=true;
				return true
			}
			this.xunXiang=false;
			return false
		},
		active:function(chd,i){
			let arr=this.showColor.slice();
			arr[i]=chd;
			this.showColor=arr;
			let jg=0;
			let t='';
			for(let o=0;o<arr.length;o++){
				jg+=new Number(arr[o].jg)
				t+=arr[o].title
				console.log(jg)
			}
			this.goShopping.price=jg
			this.txt=t
		},
		f1:function(){},
		
		shopping:function(a,b){
			let that=this;
			let usurl=that.url1+"/dinghai/shop/byid";
			uni.showLoading({
				title:'正在加载',mask: true
			})
			uni.request({
						url: usurl,
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'GET',
						data: {
							uid:a.uid
						},
				    success: (res) => {
						console.log(res)
						if(res.data.code==200){
							this.goShopping=a;
							this.shop=res.data.data;
							uni.showToast({
								title:"加载成功",
								icon:"none"
							})
						}else{
							uni.showToast({
								title:"您的网络打了个盹",
								icon:"none"
							})
						}
				    },
					fail : function(err){
						console.log(err)
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
				});
		},
		back : function(){
		   uni.navigateBack({

		   });
		},
		getshop:function(){
			let that=this;
			let usurl=that.url1+"/dinghai/commodity/getbytypeall";
			uni.showLoading({
				title:'正在加载',mask: true
			})
			uni.request({
						url: usurl,
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'GET',
						data: {
							type:that.goShopping.type,
							start:1,
							size:20
						},
				    success: (res) => {
						console.log(res)
						if(res.data.code==200){
							that.cnxh=res.data.data;
							for(let i=0;i<that.cnxh.length;i++){
								that.cnxh[i].img1=JSON.parse(that.cnxh[i].img1);
								that.cnxh[i].img2=JSON.parse(that.cnxh[i].img2);
								that.cnxh[i].shopType=JSON.parse(that.cnxh[i].shopType);
								that.cnxh[i].label=JSON.parse(that.cnxh[i].label)
							}
							uni.showToast({
								title:"加载成功",
								icon:"none"
							})
						}else{
							uni.showToast({
								title:"您的网络打了个盹",
								icon:"none"
							})
						}
				    },
					fail : function(err){
						console.log(err)
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
				});
		},
		goDianPu:function(){
			uni.navigateTo({
				url:"../dianPu/dianPu?item="+JSON.stringify(this.shop)
			})
		}
		
	}
}
